import { Link } from 'react-router-dom'

const Connect = (props) => {
  if (props.to === undefined) {
    return (
      <button
        style={{
          width: '10vw',
          margin: '5vh 0',
          alignSelf: 'center',
          color: '#d7dadc',
          // textDecoration: 'none',
          border: '1px solid green',
          padding: '1vw 1vh',
          textAlign: 'center',
          borderRadius: '2px',
          backgroundColor: 'transparent',
        }}
        onClick={props.onClick}>
        {props.txt || 'Connect'}
      </button>
    )
  } else {
    return (
      <Link
        to={props.to}
        style={{
          width: '40%',
          alignSelf: 'center',
          color: '#d7dadc',
          textDecoration: 'none',
          border: '1px solid green',
          padding: '1vw 1vh',
          textAlign: 'center',
          borderRadius: '2px',
        }}>
        Connect
      </Link>
    )
  }
}

export default Connect