import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'
import { NavDropdown } from 'react-bootstrap'
import './Nav.css'

class Nav extends Component {
  constructor() {
    super()
    this.state = {
      showMenu: false,
    }
    this.showMenu = this.showMenu.bind(this)
  }
  showMenu(e) {
    e.preventDefault()
    this.setState({ showMenu: !this.state.showMenu })
  }
  render() {
    const InfoNav = () => (
      <div style={{display: 'flex', flexDirection: 'row', justifyContent: 'space-evenly', placeItems: 'center'}}>
        <div style={{ margin: '0 .5vw 0 0' }}>Info</div>
        <div className={'classN2'}></div>
      </div>
    )
    return (
      <div className={'nav'}>
        <NavLink to={'/'} className={'navl'}>Tordano</NavLink>
        <NavLink to={'/airdrop'} className={'navl'}>Airdrop</NavLink>
        <NavLink to={'/mining'} className={'navl'}>Mining</NavLink>
        <NavLink to={'/governance'} className={'navl'}>Voting</NavLink>
        <NavLink to={'/compliance'} className={'navl'}>Compliance</NavLink>
        <NavLink to={'/stats'} className={'navl'}>Stats</NavLink>
        <NavDropdown title={<InfoNav/>} id={'basic-nav-dropdown'} className={'navl'}>
          <NavDropdown.Item href={'/#/tutorial'} className={'navl navd'}>Tutorial</NavDropdown.Item>
          <NavDropdown.Divider/>
          <NavDropdown.Item href={'/#'} className={'navl navd'}>About (medium)</NavDropdown.Item>
          <NavDropdown.Divider/>
          <NavDropdown.Item href={'/#'} className={'navl navd'}>Privacy tips (medium)</NavDropdown.Item>
        </NavDropdown>
        <NavLink to={'/changelog'} className={'navl'}>ChangeLog</NavLink> 
      </div>
    )
  }
}

export default Nav